@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;600&display=swap');


*{
    font-family: 'Nunito', sans-serif;
    margin:0; padding:0;
    box-sizing: border-box;
    text-decoration: none;
    outline: none; border:none;
    text-transform: capitalize;
    transition: all .2s linear;
}

.content-container{
    padding: 10px;
    border-radius: 50px;
}

.content-card{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    text-align: start;
    width: 75%;
    margin:5px;
    padding: 10px;
    border-radius: 50px;
    box-shadow: 0px 2px 8px 0px rgba(0,0,0,0.2);

}


.content-container .content-card img{
    width: 100%;
    max-width:300px;
    height: auto;
    max-height: 400px;
    margin: 10px;
    border-radius: 50px;
}

.content-info{
    width: auto;
    margin: 10px;
    padding: 20px;
    box-shadow: 0px 2px 8px 0px rgba(0,0,0,0.2);
    border-radius: 50px;
}
.home{
    margin: 5%;
    padding: 10px
}

.upload-button {
    width: auto;
}

.button-card{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: 10px;
    padding: 10px;

}

.btn {
    margin: 5px;
}

.content-info h5{
    font-weight: bold;
}

.content-info i{
    font-weight: 300;
    font-size: 18px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
