.paginationBttns{
    width: 80%;
    height: 40%;
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    padding: 20px
}

.paginationBttns a{
    padding: 10px;
    margin: 8px;
    text-decoration: none;
    border-radius: 5px;
    border: 1px solid #9926f0;
    color: #9926f0;
    cursor: pointer;
}

.paginationBttns a:hover{
    color: white;
    background-color: #9926f0
}

.paginationBttns{
    display:flex;
    margin: auto
}
.paginationActive{
    color:white;
}

.paginationActive a{
    color: white;
    background-color: #9926f0
}

.studentButton {
    background: #9926f0 !important;
    text-decoration: none;
    color: white !important;
    padding:10px;
    margin: 10px;
    border-radius: 50px;
   
}


.content-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 30px;
}

.content-section{
    display: flex;
    width: 250px;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.content-section .content-card{
    width: 2500px;
    height: 100%;
    margin:5px;
    padding: 10px;
    box-shadow: 0px 2px 8px 0px rgba(0,0,0,0.2);
    border-radius: 50px;
}

.image-class{
    display: flex;
    justify-content: center;
    width: 200px;
    height: 250px;
}

.image-class img{
    max-width: 100%;
    max-height: 100%;
    margin: auto;
    padding: 5%;
    border-radius: 50px;
}

.search-bar{
    box-shadow: 0px 2px 8px 0px rgba(0,0,0,0.2);
    border-radius: 50px;
    padding: 10px;
    margin: 10px;
}

.add-search{
    display: flex;
    justify-content: space-between;
}

