@import "../Variables/custom.scss";
@import "../Variables/mixins.scss";

.menu {
  @include gradient;

  li {
    padding: 0 0.7rem;

    #headlink {
      color: $white !important;
      text-transform: capitalize;
      font-weight: 600;
    }
  }
  img {
    height: 3rem;
    width: 3rem;
  }
}

.btn-rounded {
  @mixin media-breakpoint-down {
    @include media-breakpoint-down(sm) {
      display: none;
    }
  }

  background-color: $white;
  position: relative;
  padding: 0.9375rem 4.375rem 0.9375rem 1.25rem;
  font-weight: 700;
  margin: 0;
  border: 0.1rem solid rgba($white, 0.6);

  .loginLink {
    color: $black;
    text-transform: capitalize;
    font-weight: 700;
    text-decoration: none;
  }

  span {
    position: absolute;
    padding: 6.5px;
    background-color: $primary;
    width: 2.5rem;
    height: 2.5rem;
    top: 50%;
    right: 5px;
    transform: translateY(-50%);
    border-radius: 50%;

    .phone {
      color: $white;
      font-size: 1rem;
      line-height: 2.5rem;
    }
  }

  &:hover {
    @include gradient;
    color: $white;

    .loginLink {
      color: $white;
    }

    span {
      background-color: rgba($black, 0.6);
    }
  }
}
